import axios from "../axios";
import React, { useEffect, useState } from "react";
import { Pie } from "react-chartjs-2";

const SectorWiseInsights = ({ sectorsOption }) => {
  const [intensity, setIntensity] = useState([]);
  const [likelihood, setLikelihood] = useState([]);
  const [relevance, setRelevance] = useState([]);

  const [sectorWiseFilter, setSectorWiseFilter] = useState("");

  const data = {
    labels: [sectorWiseFilter],
    datasets: [
      {
        label: "Intesinity",
        data: [intensity],
        backgroundColor: ["#f255f2"],
        borderColor: "black",
        borderWidth: 2,
      },
      {
        label: "Likelihood",
        data: [likelihood],
        backgroundColor: ["#32a852"],
        borderColor: "black",
        borderWidth: 2,
      },
      {
        label: "Relevance",
        data: [relevance],
        backgroundColor: ["#2bf8ff"],
        borderColor: "black",
        borderWidth: 2,
      },
    ],
  };

  const getData = async () => {
    try {
      if (sectorWiseFilter !== undefined) {
        const res = await axios.get(`/data?sector=${sectorWiseFilter}`);

        if (res.data) {
          const intensity = res.data[0].totalIntensity;
          setIntensity(intensity);

          const likelihood = res.data[0].totalLikelihood;
          setLikelihood(likelihood);

          const relevance = res.data[0].totalRelevance;
          setRelevance(relevance);
        }
      }
    } catch (error) {}
  };

  useEffect(() => {
    setSectorWiseFilter(sectorsOption[0]);
  }, [sectorsOption]);

  useEffect(() => {
    getData();
  }, [sectorWiseFilter]);

  return (
    <div className="chart-box">
      <div>
        <h2>
          {sectorWiseFilter?.toUpperCase() + " Statistics".toUpperCase()}{" "}
        </h2>

        <br></br>
        <select
          style={{ padding: "10px" }}
          onChange={(e) => setSectorWiseFilter(e.target.value)}
        >
          {sectorsOption.map((topic, i) => (
            <option key={i}>{topic}</option>
          ))}
        </select>
      </div>
      <div style={{ width: "26vw", margin: "auto" }}>
        <Pie
          data={data}
          options={{
            skipNull: true,
            responsive: true,
            maintainAspectRatio: true,
          }}
        />
      </div>
    </div>
  );
};

export default SectorWiseInsights;
