import axios from "../axios";
import React, { useEffect, useState } from "react";
import { Line } from "react-chartjs-2";

import "./chart.css";

const TopicWiseInsights = ({ topicsOption, setTopicsOption }) => {
  const [intensity, setIntensity] = useState([]);
  const [likelihood, setLikelihood] = useState([]);
  const [relevance, setRelevance] = useState([]);
  const [insight, setInsight] = useState([]);

  const [topicWiseFilter, setTopicWiseFilter] = useState("");

  const data = {
    labels: insight,
    datasets: [
      {
        label: "Intensity",
        fill: false,
        lineTension: 0.1,
        backgroundColor: "rgba(75,192,192,0.4)",
        borderColor: "rgba(75,192,192,1)",
        borderCapStyle: "butt",
        borderDash: [],
        borderDashOffset: 0.0,
        borderJoinStyle: "miter",
        pointBorderColor: "rgba(75,192,192,1)",
        pointBackgroundColor: "#fff",
        pointBorderWidth: 1,
        pointHoverRadius: 5,
        pointHoverBackgroundColor: "rgba(75,192,192,1)",
        pointHoverBorderColor: "rgba(220,220,220,1)",
        pointHoverBorderWidth: 2,
        pointRadius: 1,
        pointHitRadius: 10,
        data: intensity,
      },
      {
        label: "Intensity",
        fill: false,
        lineTension: 0.1,
        backgroundColor: "rgba(75,192,192,0.4)",
        borderColor: "#eb8a8f",
        borderCapStyle: "butt",
        borderDash: [],
        borderDashOffset: 0.0,
        borderJoinStyle: "miter",
        pointBorderColor: "#bf020b",
        pointBackgroundColor: "#fff",
        pointBorderWidth: 1,
        pointHoverRadius: 5,
        pointHoverBackgroundColor: "rgba(75,192,192,1)",
        pointHoverBorderColor: "rgba(220,220,220,1)",
        pointHoverBorderWidth: 2,
        pointRadius: 1,
        pointHitRadius: 10,
        data: likelihood,
      },
      {
        label: "Intensity",
        fill: false,
        lineTension: 0.1,
        backgroundColor: "rgba(75,192,192,0.4)",
        borderColor: "rgba(75,192,192,1)",
        borderCapStyle: "butt",
        borderDash: [],
        borderDashOffset: 0.0,
        borderJoinStyle: "miter",
        pointBorderColor: "rgba(75,192,192,1)",
        pointBackgroundColor: "#fff",
        pointBorderWidth: 1,
        pointHoverRadius: 5,
        pointHoverBackgroundColor: "rgba(75,192,192,1)",
        pointHoverBorderColor: "rgba(220,220,220,1)",
        pointHoverBorderWidth: 2,
        pointRadius: 1,
        pointHitRadius: 10,
        data: relevance,
      },
    ],
  };

  const getData = async () => {
    try {
      if (topicWiseFilter != undefined) {
        const res = await axios.get(`/data?topic=${topicWiseFilter}`);

        if (res.data) {
          const intensity = res.data.map((d) => d.intensity);
          setIntensity(intensity);

          const likelihood = res.data.map((d) => d.likelihood);
          setLikelihood(likelihood);

          const relevance = res.data.map((d) => d.relevance);
          setRelevance(relevance);

          const insight = res.data.map((d) => d.insight.slice(0, 20) + "...");
          setInsight(insight);
        }
      }
    } catch (error) {}
  };

  useEffect(() => {
    setTopicWiseFilter(topicsOption[0]);
  }, [topicsOption]);

  useEffect(() => {
    getData();
  }, [topicWiseFilter]);

  return (
    <div className="chart-box">
      <h2>{topicWiseFilter?.toUpperCase()} Insights</h2>
      <div>
        <br></br>
        <select
          style={{ padding: "10px" }}
          onChange={(e) => setTopicWiseFilter(e.target.value)}
        >
          {topicsOption.map((topic, i) => (
            <option key={i}>{topic}</option>
          ))}
        </select>
      </div>
      <Line data={data} />
    </div>
  );
};

export default TopicWiseInsights;
