import axios from "../axios";
import React, { useEffect, useState } from "react";
import { PolarArea } from "react-chartjs-2";

const PestleFilter = ({ pestlesOption }) => {
  const [intensity, setIntensity] = useState();
  const [likelihood, setLikelihood] = useState();
  const [relevance, setRelevance] = useState();

  const [pistleFilter, setPistleFilter] = useState("");

  const data = {
    labels: ["intensity", "likelihood", "relevance"],

    datasets: [
      {
        label: "insight",
        data: [intensity, likelihood, relevance],
        backgroundColor: [
          "rgb(255, 99, 132)",
          "rgb(75, 192, 192)",
          "rgb(255, 205, 86)",
          "rgb(201, 203, 207)",
          "rgb(54, 162, 235)",
        ],
      },
    ],
  };

  const getData = async () => {
    try {
      if (pistleFilter !== undefined) {
        const res = await axios.get(`/data?pest=${pistleFilter}`);

        if (res.data) {
          const intensity = res.data
            .map((d) => d.intensity)
            .reduce((sum, a) => sum + a, 0);
          setIntensity(intensity);

          const likelihood = res.data
            .map((d) => d.likelihood)
            .reduce((sum, a) => sum + a, 0);
          setLikelihood(likelihood);

          const relevance = res.data
            .map((d) => d.relevance)
            .reduce((sum, a) => sum + a, 0);

          setRelevance(relevance);
        }
      }
    } catch (error) {}
  };

  useEffect(() => {
    setPistleFilter(pestlesOption[0]);
  }, [pestlesOption]);

  useEffect(() => {
    getData();
  }, [pistleFilter]);

  return (
    <div className="chart-box">
      <h2>{pistleFilter?.toUpperCase() + " Insights".toUpperCase()} </h2>
      <div>
        <br></br>
        <select
          style={{ padding: "10px" }}
          onChange={(e) => setPistleFilter(e.target.value)}
        >
          {pestlesOption.map((year, i) => (
            <option key={i}>{year}</option>
          ))}
        </select>
      </div>
      <div style={{ width: "25vw", margin: "auto" }}>
        <PolarArea
          data={data}
          options={{
            responsive: true,
            maintainAspectRatio: true,
          }}
        />
      </div>
    </div>
  );
};

export default PestleFilter;
