import axios from "../axios";
import React, { useEffect, useState } from "react";
import { Bar } from "react-chartjs-2";

const SourceFIlter = ({ sourcesOption }) => {
  const [intensity, setIntensity] = useState([]);
  const [likelihood, setLikelihood] = useState([]);
  const [relevance, setRelevance] = useState([]);
  const [insight, setInsight] = useState([]);

  const [sourceFilter, setSourceFilter] = useState("");

  const data = {
    labels: insight,
    datasets: [
      {
        label: "Intensity",
        backgroundColor: "rgba(201, 203, 207, 0.2)",
        borderColor: "rgb(201, 203, 207)",
        borderWidth: 1,
        hoverBackgroundColor: "rgba(255,99,132,0.4)",
        hoverBorderColor: "rgba(255,99,132,1)",
        data: intensity,
      },
      {
        label: "Likelihood",
        backgroundColor: "rgba(75, 192, 192, 0.2)",
        borderColor: "rgb(75, 192, 192)",
        borderWidth: 1,
        hoverBackgroundColor: "rgba(75, 192, 192, 0.6)",
        hoverBorderColor: "rgba(75, 192, 192, 0.2)",
        data: likelihood,
      },
      {
        label: "Relevance",
        backgroundColor: "rgba(153, 102, 255, 0.2)",
        borderColor: "rgb(153, 102, 255)",
        borderWidth: 1,
        hoverBackgroundColor: "rgb(153, 102, 255,0.6)",
        hoverBorderColor: "rgb(153, 102, 255,0.2)",
        data: relevance,
      },
    ],
  };

  const getData = async () => {
    try {
      if (sourceFilter !== undefined) {
        const res = await axios.get(`/data?source=${sourceFilter}`);

        if (res.data) {
          const intensity = res.data.map((d) => d.intensity);
          setIntensity(intensity);

          const likelihood = res.data.map((d) => d.likelihood);
          setLikelihood(likelihood);

          const relevance = res.data.map((d) => d.relevance);
          setRelevance(relevance);

          const insight = res.data.map((d) => d.insight.slice(0, 20) + "...");
          setInsight(insight);
        }
      }
    } catch (error) {}
  };

  useEffect(() => {
    setSourceFilter(sourcesOption[0]);
  }, [sourcesOption]);

  useEffect(() => {
    getData();
  }, [sourceFilter]);

  return (
    <div className="chart-box">
      <h2>{sourceFilter} Insights</h2>
      <div>
        <br></br>
        <select
          style={{ padding: "10px" }}
          onChange={(e) => setSourceFilter(e.target.value)}
        >
          {sourcesOption.map((year, i) => (
            <option key={i}>{year}</option>
          ))}
        </select>
      </div>
      <Bar
        data={data}
        width={"90%"}
        height={50}
        options={{
          skipNull: true,
          responsive: true,
        }}
      />
    </div>
  );
};

export default SourceFIlter;
