import "./App.css";

import React, { useEffect, useState } from "react";

import axios from "./axios";

import {
  ArcElement,
  BarElement,
  CategoryScale,
  Chart as ChartJS,
  Legend,
  LineElement,
  LinearScale,
  PointElement,
  RadialLinearScale,
  Title,
  Tooltip,
} from "chart.js";

import EndYearInsights from "./Components/EndYearInsights";
import TopicWiseInsights from "./Components/TopicWiseInsights";
import SectorWiseInsights from "./Components/SectorWiseInsights";
import RegionFIlter from "./Components/RegionFIlter";
import PestleFilter from "./Components/PestleFilter";
import SourceFIlter from "./Components/SourceFIlter";

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  RadialLinearScale,
  PointElement,
  ArcElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

function App() {
  const [topicsOption, setTopicsOption] = useState([]);
  const [endYearsOption, setEndYearOption] = useState([]);
  const [sectorsOption, setSectorsOption] = useState([]);
  const [pestlesOption, setPestlesOption] = useState([]);
  const [sourcesOption, setSourcesOption] = useState([]);
  const [countriesOption, setCountriesOption] = useState([]);
  const [regionsOption, setRegionsOption] = useState([]);

  const data2 = {
    labels: ["Red", "Green", "Yellow"],
    datasets: [
      {
        data: [300, 50, 100],
        backgroundColor: ["#FF6384", "#36A2EB", "#FFCE56"],
        hoverBackgroundColor: ["#FF6384", "#36A2EB", "#FFCE56"],
      },
    ],
  };

  const getOptions = async () => {
    try {
      const res = await axios.get(`/options`);

      if (res.data) {
        const {
          topics,
          endYears,
          sectors,
          pestles,
          sources,
          countries,
          regions,
        } = res.data;

        setTopicsOption(topics);
        setEndYearOption(endYears);
        setSectorsOption(sectors);
        setPestlesOption(pestles);
        setSourcesOption(sources);
        setCountriesOption(countries);
        setRegionsOption(regions);
      }
    } catch (error) {}
  };

  useEffect(() => {
    getOptions();
  }, []);

  return (
    <>
      <div className="row">
        <EndYearInsights endYearsOption={endYearsOption}></EndYearInsights>
        <TopicWiseInsights topicsOption={topicsOption}></TopicWiseInsights>
      </div>
      <div className="row">
        <SectorWiseInsights sectorsOption={sectorsOption}></SectorWiseInsights>
      </div>
      <div className="row">
        <RegionFIlter regionsOption={regionsOption}></RegionFIlter>

        <PestleFilter pestlesOption={pestlesOption}></PestleFilter>
      </div>

      <div className="row">
        <SourceFIlter sourcesOption={sourcesOption}></SourceFIlter>
        {/* <TopicWiseInsights topicsOption={topicsOption}></TopicWiseInsights> */}
      </div>
    </>
  );
}

export default App;
