import axios from "../axios";
import React, { useEffect, useState } from "react";
import { Radar } from "react-chartjs-2";

const RegionFIlter = () => {
  const [intensity, setIntensity] = useState([]);
  const [likelihood, setLikelihood] = useState([]);
  const [relevance, setRelevance] = useState([]);
  const [regions, setRegions] = useState([]);

  const data = {
    labels: regions,
    datasets: [
      {
        label: "Intensity",
        data: intensity,
        fill: true,
        backgroundColor: "rgba(255, 99, 132, 0.2)",
        borderColor: "rgb(255, 99, 132)",
        pointBackgroundColor: "rgb(255, 99, 132)",
        pointBorderColor: "#fff",
        pointHoverBackgroundColor: "#fff",
        pointHoverBorderColor: "rgb(255, 99, 132)",
      },
      {
        label: "Likelihood",
        data: likelihood,
        fill: true,
        backgroundColor: "rgba(54, 162, 235, 0.2)",
        borderColor: "rgb(54, 162, 235)",
        pointBackgroundColor: "rgb(54, 162, 235)",
        pointBorderColor: "#fff",
        pointHoverBackgroundColor: "#fff",
        pointHoverBorderColor: "rgb(54, 162, 235)",
      },
      {
        label: "relevance",
        data: relevance,
        fill: true,
        backgroundColor: "#f29c55",
        borderColor: "#f29c55",
        pointBackgroundColor: "rgb(54, 162, 235)",
        pointBorderColor: "#f29c55",
        pointHoverBackgroundColor: "#fff",
        pointHoverBorderColor: "rgb(54, 162, 235)",
      },
    ],
  };

  const getData = async () => {
    try {
      const res = await axios.get(`/data`);

      if (res.data) {
        let intensity = res.data.map((d) => d.intensity);
        setIntensity(intensity);

        intensity = [...new Set(intensity)].filter((d) => d !== "");
        setRelevance(intensity);

        let likelihood = res.data.map((d) => d.likelihood);
        setLikelihood(likelihood);

        likelihood = [...new Set(likelihood)].filter((d) => d !== "");
        setRelevance(likelihood);

        let relevance = res.data.map((d) => d.relevance);

        relevance = [...new Set(relevance)].filter((d) => d !== "");
        setRelevance(relevance);

        let regions = res.data.map((d) => d.region);

        regions = [...new Set(regions)].filter((d) => d !== "");
        console.log(regions);
        setRegions(regions);
      }
    } catch (error) {}
  };

  useEffect(() => {
    getData();
  }, []);

  return (
    <div className="chart-box">
      <h2>{"Region Wise Insights".toUpperCase()}</h2>
      <div style={{ width: "30vw", margin: "auto" }}>
        <Radar
          data={data}
          options={{ responsive: true, maintainAspectRatio: true }}
        />
      </div>
    </div>
  );
};

export default RegionFIlter;
