import axios from "../axios";
import React, { useEffect, useState } from "react";
import { Bar } from "react-chartjs-2";

const EndYearInsights = ({ endYearsOption }) => {
  const [intensity, setIntensity] = useState([]);
  const [likelihood, setLikelihood] = useState([]);
  const [relevance, setRelevance] = useState([]);
  const [insight, setInsight] = useState([]);

  const [endyearFilter, setEndyearFilter] = useState("");

  const data = {
    labels: insight,
    datasets: [
      {
        label: "Intensity",
        backgroundColor: "rgba(255,99,132,0.2)",
        borderColor: "rgba(255,99,132,1)",
        borderWidth: 1,
        hoverBackgroundColor: "rgba(255,99,132,0.4)",
        hoverBorderColor: "rgba(255,99,132,1)",
        data: intensity,
      },
      {
        label: "Likelihood",
        backgroundColor: "#12a2b5",
        borderColor: "rgba(0,0,0,0.4)",
        borderWidth: 1,
        hoverBackgroundColor: "rgba(255,99,132,0.4)",
        hoverBorderColor: "rgba(255,99,132,1)",
        data: likelihood,
      },
      {
        label: "Relevance",
        backgroundColor: "#7cd408",
        borderColor: "rgba(255,99,132,1)",
        borderWidth: 1,
        hoverBackgroundColor: "rgba(255,99,132,0.4)",
        hoverBorderColor: "rgba(255,99,132,1)",
        data: relevance,
      },
    ],
  };

  const getData = async () => {
    try {
      if (endyearFilter != undefined) {
        const res = await axios.get(`/data?endyear=${endyearFilter}`);

        if (res.data) {
          const intensity = res.data.map((d) => d.intensity);
          setIntensity(intensity);

          const likelihood = res.data.map((d) => d.likelihood);
          setLikelihood(likelihood);

          const relevance = res.data.map((d) => d.relevance);
          setRelevance(relevance);

          const insight = res.data.map((d) => d.insight.slice(0, 20) + "...");
          setInsight(insight);
        }
      }
    } catch (error) {}
  };

  useEffect(() => {
    setEndyearFilter(endYearsOption[0]);
  }, [endYearsOption]);

  useEffect(() => {
    getData();
  }, [endyearFilter]);

  return (
    <div className="chart-box">
      <h2>Year {endyearFilter} Insights</h2>
      <div>
        <br></br>
        <select
          style={{ padding: "10px" }}
          onChange={(e) => setEndyearFilter(e.target.value)}
        >
          {endYearsOption.map((year, i) => (
            <option key={i}>{year}</option>
          ))}
        </select>
      </div>
      <Bar
        data={data}
        width={"90%"}
        height={50}
        options={{
          skipNull: true,
          responsive: true,
        }}
      />
    </div>
  );
};

export default EndYearInsights;
